import React from "react";
import _ from 'lodash';

import { ThemeProvider, withStyles } from '@material-ui/styles';
import { Grid, Paper, Typography, Divider, Container, CssBaseline } from '@material-ui/core';

import Header from './components/Header';
import Footer from './components/Footer';
import Job from './components/Job';

import aboutData from './common/data/about';
import skillsetData from './common/data/skillset';
import experienceData from './common/data/experience';
import theme from './common/theme/DefaultTheme';
import styles from './common/theme/Styles';

import WorkSamples from "./containers/WorkSamples";
import Dialogs from "./components/Dialogs";

import './scss/importer.scss';
import 'video-react/styles/scss/video-react.scss';

class App extends React.Component {
  render() {
    const { classes } = this.props;

    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        
        <Container maxWidth="lg">

          <Paper className={classes.paper}>

            <Grid container spacing={3} justify="center">
              <Grid item sm={12}>
                <Header/>

                <Typography variant="h1" gutterBottom align="center">Tommy Rosario</Typography>


                  <section className={classes.section}>
                    <Typography variant="h2" gutterBottom>Hello</Typography>
                    {
                      _.map(aboutData, (para, idx) =>
                        <Typography variant="body1" gutterBottom paragraph key={idx}>{para}</Typography>
                      )
                    }
                  </section>

                  <Divider className={classes.divider} />

                  <section className={classes.section}>
                    <Typography variant="h2" gutterBottom>Work Samples</Typography>
                    <Typography variant="body1" gutterBottom paragraph>
                      This a hand-picked collection of some recent work I've had the pleasure of being involved with building.  Enjoy!
                    </Typography>
                    <WorkSamples />
                  </section>

                  <Divider className={classes.divider} />

                  <section className={classes.section}>
                    <Typography variant="h2" gutterBottom>Skillset</Typography>
                    {
                      _.map(skillsetData, (para, idx) =>
                        <Typography variant="body1" gutterBottom paragraph key={idx}>{para}</Typography>
                      )
                    }
                  </section>

                  <Divider className={classes.divider} />
                 
                  <section className={classes.section}>
                    <Typography variant="h2" gutterBottom>Experience</Typography>

                    {
                      _.map(experienceData, (job, idx) =>
                        <Job data={job} key={idx} />
                      )
                    }
                  </section>

              </Grid>

            </Grid> 
                
          </Paper>
          
          <Footer/>

        </Container>

        <Dialogs />
        
      </ThemeProvider>
    )
  }
}

export default withStyles(styles)(App);
