import React from "react";

import _ from "lodash";

import { Dialog as MUIDialog } from '@material-ui/core';

const OMIT_PROPS = [
  'dismissSelf',
  'onEnter',
  'onClose',
  'children'
];

export default class Dialog extends React.Component {
  componentWillReceiveProps(nextProps) {
    if (!this.props.open && nextProps.open) { this._open(); }
    if (this.props.open && !nextProps.open) { this._dismiss(); }
  }

  _open() {
    if (this.props.onEnter) this.props.onEnter();
  }

  _dismiss() {
   this.handleDismiss();
  }

  handleDismiss = () => {
    if (this.props.dismissSelf) this.props.dismissSelf();
    if (this.props.onClose) this.props.onClose();
  }

  render() {
    return (
      <MUIDialog
        {..._.omit(this.props, OMIT_PROPS)}
        onClose={this.handleDismiss}
      >
        {this.props.children}
      </MUIDialog>
    );
  }
}
