import {
  SELECT_ITEM
} from "../actionTypes";

import WORK_SAMPLES from '../common/data/work-samples';

const defaultState = () => {
  return {
    collection: WORK_SAMPLES,
    selected: null
  };
}

const items = (state = defaultState(), action) => {

  switch (action.type) {
    case SELECT_ITEM:
      return {
        ...state,
        selected: action.item
      };

    default:
      return state;
  }

}

export default items;
