import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import styles from '../common/theme/Styles';


class Job extends React.Component {
 render() {
    const { data, classes } = this.props;

    return (
      <div className={classes.subsection}>
        <Typography variant="h3" gutterBottom>{data.company}, {data.title}</Typography>
        <Typography variant="h4" gutterBottom>{data.date}</Typography>

        {
          _.map(data.description, (para, idx) =>
            <Typography variant="body1" gutterBottom key={idx}>
              {para}
            </Typography>
          )
        }

        <Typography variant="body1"><strong>Technology:</strong> {data.technology}</Typography>
      </div>
    )
  }
}

export default withStyles(styles)(Job);
