import { connect } from "react-redux";

import * as DialogActionCreators from './actions/dialogs';

export default function connectDialog(name, select, actions) {
  if (!select) select = state => ({});
  if (!actions) actions = {};

  return connect(
    state => ({
      ...state.dialogs[name],
      open: !!state.dialogs[name],
      ...select(state)
    }),
    {
      ...actions,
      ...DialogActionCreators,
      dismissSelf: () => DialogActionCreators.dismissDialog(name)
    }
  );
}
