import {
  SELECT_ITEM,
  DISMISS_DIALOG
} from "../actionTypes";

const dialogs = (state = {}, action) => {

switch (action.type) {

  case DISMISS_DIALOG:
    return {
      ...state, 
      [action.name]: null
    };

  case SELECT_ITEM:
    return {
      ...state,
      workSample: {
        item: action.item
      }
    };

  default:
    return state;
  }

};

export default dialogs;
