import React from "react";

import { 
  Button,
  DialogContent, 
  DialogContentText, 
  DialogTitle, 
  DialogActions,
  IconButton
} from '@material-ui/core';

import Dialog from '../components/Dialog';
import connectDialog from '../connectDialog';

import { Player } from 'video-react';
import WorkSampleActions from '../components/WorkSampleActions';

@connectDialog('workSample')
export default class WorkSampleDialog extends React.Component {
  render() {
    const { item } = this.props;

    return (
      <Dialog
        open={this.props.open}
        dismissSelf={this.props.dismissSelf}
        maxWidth={'xl'}
      >
        {item ?
        <>
          <DialogTitle>{item.title}</DialogTitle>
          <DialogContent>

            <div className="media">
              
              <Player
                playsInline
                autoPlay
                src={item.src}
              />
              
            </div>

          </DialogContent>

          <DialogActions>
            <WorkSampleActions item={item} />

            <IconButton className="fas fa-times" onClick={this.props.dismissSelf} />
          </DialogActions>
        </>
        : 
        <div>loading...</div>
        }
      </Dialog>
    );
  }
}
