import React from 'react';

import { 
  IconButton
} from '@material-ui/core';


export default function WorkSampleActions(props) {
  const { item } = props;

  return (
    <>
      {item.github ?
      <a href={item.github} target="_blank" className="github"><IconButton className="fab fa-github" /></a>
      : null}

      {item.url ?
      <a href={item.url} target="_blank" className="url"><IconButton className="fas fa-external-link-alt" /></a>
      : null}
    </>
  );
}
