import React from "react";
import { connect } from 'react-redux';

import { 
  Typography, 
  IconButton, 
  Button, 
  Grid, 
  Card, 
  CardActionArea, 
  CardActions, 
  CardContent, 
  CardMedia,
} from '@material-ui/core';

import WorkSampleActions from '../components/WorkSampleActions';
import * as ItemActionCreators from '../actions/items';

const mapStateToProps = state => ({
  items: state.items,
  modals: state.modals
});

const mapDispatchToProps = dispatch => ({
  selectItem: item => dispatch(ItemActionCreators.selectItem(item))
});

@connect(mapStateToProps, mapDispatchToProps)
export default class WorkSamples extends React.Component {

  render() {
    const { items, selectItem } = this.props;

    return (
      <Grid container spacing={3} justify="center">
        {items.collection.map(item =>
          <Grid item sm={6} key={item.id}>

            <Card>
              <CardActionArea>
                <CardMedia
                  className={'card-media'}
                  image={item.preview}
                  title={item.title}
                  onClick={()=> selectItem(item)}
                />
                <CardContent onClick={()=> selectItem(item)} className="card-content">
                  <Typography gutterBottom variant="h5" component="h2">
                    {item.title}
                  </Typography>
                  <Typography variant="body2" color="textSecondary" component="p">
                    {item.description}
                  </Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <WorkSampleActions item={item} />
                <IconButton className="fas fa-video" onClick={()=> selectItem(item)} />
              </CardActions>
            </Card>

          </Grid>
        )}
      </Grid>
    );
  }

}
